.cardShow {
  z-index: 3000001;
  animation: .4s linear both cardShow;
}

.cardHide {
  opacity: 0;
  pointer-events: none;
}

.glitch {
  color: var(--outerspace);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.line {
  display: none;
}

.lastLine {
  opacity: 1;
  display: block;
}

.glitchOn .line {
  will-change: clip-path;
  display: block;
}

.glitchOn .line:not(:first-child) {
  position: absolute;
}

.glitchOn .line:first-child {
  animation: 3s linear -300ms infinite clip, 1s linear -228ms infinite glitch1;
}

.glitchOn .line:nth-child(2) {
  animation: 3s linear -600ms infinite clip, 1s linear -927ms infinite glitch2;
}

.line:nth-child(3) {
  animation: 3s linear -900ms infinite clip, 1s linear -496ms infinite glitch3;
}

.line:nth-child(4) {
  animation: 3s linear -1200ms infinite clip, 1s linear -660ms infinite glitch4;
}

.line:nth-child(5) {
  animation: 3s linear -11000ms infinite clip, 1s linear -399ms infinite glitch5;
}

.line:nth-child(6) {
  animation: 3s linear -1800ms infinite clip, 1s linear -657ms infinite glitch6;
}

.line:nth-child(7) {
  animation: 3s linear -2100ms infinite clip, 1s linear -619ms infinite glitch7;
}

.line:nth-child(8) {
  animation: 3s linear -2400ms infinite clip, 1s linear -380ms infinite glitch8;
}

.line:nth-child(9) {
  animation: 3s linear -2700ms infinite clip, 1s linear -863ms infinite glitch9;
}

.line:nth-child(10) {
  animation: 3s linear -3000ms infinite clip, 1s linear -745ms infinite glitch10;
}

