.dropdown {
  letter-spacing: 1px;
  text-transform: uppercase;
  width: 130px;
  height: 28px;
  transition: var(--standardFast), 0s linear background;
  z-index: 2147483647;
  pointer-events: auto;
  cursor: pointer;
  box-shadow: 0px 1px 4px -1px var(--ghostX);
  background: #0c3344;
  border-bottom: 2px solid #002739;
  border-radius: 24px;
  padding: 0;
  font-family: MainFont;
  font-size: 13px;
  font-weight: 300;
  overflow: hidden;
  transform: translateZ(0);
}

.dropdown:hover, .darkerDrop {
  background: #002739;
}

.darkerDrop .dropdownItem .dropdownItemIndicator {
  background: #0c3344;
}

.dropdownExpanded {
  box-shadow: 0px 8px 16px 0px var(--ghostX);
  padding: 18px 0;
}

.dropdownItems {
  transition: var(--standardFast);
  transform: translateZ(0);
}

.dropdownItem {
  box-sizing: border-box;
  z-index: 2147483647;
  cursor: pointer;
  align-items: center;
  width: calc(100% - 20px);
  height: 28px;
  min-height: 28px;
  margin: 0 9px 0 12px;
  font-weight: 400;
  display: flex;
  position: relative;
  overflow: hidden;
  transform: translateZ(0);
}

.dropdownItem * {
  pointer-events: none;
}

.dropdownItem:before, .dropdownItem:after {
  pointer-events: none;
  content: "";
  flex: 1;
}

.dropdownItem .dropdownItemIndicator {
  background: #0c3344;
  border-radius: 3.5px;
  width: 7px;
  height: 7px;
  position: absolute;
  top: 11px;
  left: 0;
}

.dropdownItem .dropdownItemIndicatorBad {
  background: var(--bad);
}

.dropdownItem .dropdownItemIndicatorGood {
  background: var(--good);
}

